<template>
  <button class="mui-button" :class="['mui-button-' + type, 'mui-button-' + size, {'is-disabled': disabled}]">
    <span class="mui-button-text">
      <slot></slot>
    </span>
  </button>  
</template>
<script>
export default {
  props: {
    type: {
      type: String,
      default: 'primary' // primary (通用) outline (边框)
    },
    size: {
      type: String,
      default: 'large' // large (100%)  normall(内联)  small (内联 小)
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      visible: false
    }
  },
  computed: {
    
  }
}
</script>
<style lang="scss">
  .mui-button {
    position: relative;
    appearance: none;
    border-radius: 4px;
    border: 0;
    box-sizing: border-box;
    font-size: 18px;
    height: 41px;
    outline: 0;
    overflow: hidden;
    text-align: center;
    &:after {
      background-color: #000;
      content: " ";
      opacity: 0;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
    }
    &:not(.is-disabled):active:after {
      opacity: .2;
    }
  }
  .mui-button-primary {
    color: #fff;
    background-color: #008FFF;
  }
  .mui-button-outline {
    color: #008FFF;
    border: 1px solid #008FFF;
    background-color: transparent;
  }
  .mui-button-large {
    display: block;
    width: 100%;
  }
  .mui-button-normall {
    display: inline-block;
    padding: 0 12px;
  }
  .mui-button-small {
    display: inline-block;
    padding: 0 12px;
    height: 33px;
    font-size: 14px;
  }
  .mui-button.is-disabled {
    opacity: .6;
  }

</style>

