<template>
  <transition name="mui-toast-pop">
    <div class="mui-toast" v-show="visible" :class="customClass">
      <span class="mui-toast-text">{{message}}</span>
    </div>
  </transition>
  
</template>
<script>
export default {
  props: {
    message: {
      type: String
    },
    className: {
      type: String
    },
    position: {
      type: String,
      default: 'bottom'
    }
  },
  data () {
    return {
      visible: false
    }
  },
  computed: {
    customClass () {
      let classList = []
      switch (this.position) {
        case 'top':
          classList.push('is-placetop')
          break
        case 'middle':
          classList.push('is-placemiddle')
          break
        default:
          classList.push('is-placebottom')
          break
      }
      classList.push(this.className)
      return classList.join(' ')
    }
  }
}
</script>
<style lang="scss">
  .mui-toast {
    // position: fixed;
    position: absolute;
    max-width: 80%;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    color: #fff;
    box-sizing: border-box;
    text-align: center;
    z-index: 1000;
    transition: opacity .3s linear;
  }
</style>

