<template>
  <div class="mui-header" :class="{'is-fixed': fixed}">
    <div class="mui-header-left">
      <slot name="headerLeft" v-if="!hideLeft">
        <i class="mui mui-back"></i>
      </slot>
    </div>
    <h1 class="mui-header-center" v-html="title"></h1>
    <div class="mui-header-right">
      <slot name="headerRight" v-if="!hideRight">
        <i class="mui mui-home"></i>
      </slot>
    </div>
  </div>
</template>
 
<script>
export default {
  name: 'm-header',
  props: {
    fixed: {
      type: Boolean,
      default: true
    },
    hideLeft: {
      type: Boolean,
      default: false
    },
    hideRight: {
      type: Boolean,
      default: false
    },
    title: {
      default: ''
    }
  },
  data () {
    return {}
  },
  methods: {}
}
</script>
 
<style lang='scss' scoped>
 .mui-header {
   display: flex;
   align-items: center;
   padding: 0 15px;
   height: 40px;
   font-size: 14px;
   background-color: #008FFF;
   color: #fff;
   &.is-fixed {
     position: fixed;
     top: 0;
     left: 0;
     width: 100%;
   }
   .mui-header-left {
     flex: .3;
     text-align: left;
   }
   .mui-header-center {
     flex: 1;
     overflow: hidden;
     text-overflow: ellipsis;
     white-space: nowrap;
     text-align: center;
   }
   .mui-header-right {
     flex: .3;
     text-align: right;
   }
   .mui-back, .mui-home {
       font-size: 20px;
     }
 }
</style>